import { all, takeLatest, select } from 'redux-saga/effects'
import * as storageManager from '../storage/localStorage'

function* saveGame () {
  const rounds = yield select(({game}) => game.rounds);
  const currentWarBand = yield select(({game}) => game.currentWarBand);
  const currentRound = yield select(({game}) => game.currentRound);
  storageManager.saveGame({
    rounds,
    currentRound,
    currentWarBand
  });
  console.log("game saved");
}

export default function* gameSaga() {
  yield all([
    takeLatest('GAME-REDUCER/SAVE-GAME', saveGame)
  ])
};